.wrapper {
  font-family: "Barlow Condensed", sans-serif;
  .home-wrapper {
    display: flex;
    background-color: #fefefe;
    position: relative;
    justify-content: center;
    h1 {
      position: absolute;
      z-index: 2;
      margin-top: 20px;
      font-size: 50px;
      color: #c9a26f;
      text-shadow: 6px 5px 7px #3e2a10;
      text-transform: uppercase;
    }
    .video {
      height: 100vh;
      width: 500px;
      &.intro {
        position: absolute;
        animation: apperance 200ms;
        &:hover {
          filter: blur(0);
        }
      }
      &.home {
        position: relative;
      }
      &.isRight {
        position: absolute;
        animation: isRight 1.5s linear;
        right: 0;
      }
      &.backRight {
        position: absolute;
        right: 34%;
        animation: backRight 1.5s linear;
      }
      @keyframes backRight {
        from {
          right: 0;
        }
        to {
          right: 34%;
        }
      }
      @keyframes isRight {
        from {
          right: 34%;
        }
        to {
          right: 0;
        }
      }
      @keyframes apperance {
        from {
          opacity: 0;
        }
        to {
          opacity: 0.5;
        }
      }
    }
  }
  .infor {
    position: absolute;
    top: 45%;
    left: 12%;
    z-index: 4;
    animation: onbottom 1s linear;
    &.infor_isClick {
      animation: ontop 1s linear;
      top: 20%;
    }
    @keyframes ontop {
      from {
        top: 45%;
      }
      to {
        top: 20%;
      }
    }
    @keyframes onbottom {
      from {
        top: 20%;
      }
      to {
        top: 45%;
      }
    }
  }
}
